import React, { useState } from "react";
import classes from "./Filter.module.css";

const Filter = (props) => {
  const filterItems = [
    { name: "", id: 1 },
    { name: "", id: 2 },
    { name: "", id: 3 },
    { name: "", id: 4 },
    { name: "", id: 5 },
    { name: "", id: 6 },
    { name: "", id: 7 },
    { name: "", id: 8 },
    { name: "", id: 9 },
    { name: "", id: 10 },
    { name: "", id: 11 },
    { name: "", id: 12 },
    { name: "", id: 13 },
    { name: "", id: 14 },
    { name: "", id: 15 },
    { name: "", id: 16 },
    { name: "", id: 17 },
    { name: "", id: 18 },
    { name: "", id: 19 },
    { name: "", id: 20 },
    { name: "", id: 21 },
    { name: "", id: 22 },
    { name: "", id: 23 },
    { name: "", id: 24 },
    { name: "", id: 25 },
    { name: "", id: 26 },
    { name: "", id: 27 },
    { name: "", id: 28 },
    { name: "", id: 29 },
  ];

  const [activeFilterItem, setActiveFilterItem] = useState({
    active: false,
    itemId: null,
  });

  const onFilterItemClick = (event) => {
    setActiveFilterItem({ active: true, itemId: event.target.id });

    props.onFilterClick(event.target.innerHTML);
  };

  return (
    <ul className={classes.region_search__filter}>
      {filterItems.map((item) => (
        <li
          key={item.id}
          id={item.id}
          className={
            activeFilterItem.active && activeFilterItem.itemId == item.id
              ? classes.focus
              : ""
          }
          onClick={onFilterItemClick}
        >
          {item.name}
        </li>
      ))}
    </ul>
  );
};

export default Filter;
